/* bg-dark text-light p-4 */

.footer {
	left: 0;
	bottom: 0;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.788);
	color: white;
	text-align: center;
	padding: 12px;
}

.navbar {
	background-color: beige !important;
}

.register-page {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 90vh;
}

/* accounts page */
.filters {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 15px 20px;
	box-shadow: 0 0 3px gray;
}

.center {
	margin: auto;
	width: 20%;
	/* border: 3px solid green; */
	padding: 20px;
}

.switch-icons {
	border: 1px solid rgba(0, 0, 0, 0.555);
	border-radius: 5px;
	padding: 10px 15px;
}

.anticon svg {
	font-size: 20px;
	cursor: pointer;
}

.active-icon {
	color: black;
}

.active-icon {
	color: gray;
}
